* {
  outline: none;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  font-size: 12px;
  background-color: #8b0000;
  margin-right: 20px;
}
/* @media only screen and (max-width: 414px) {
  .primary-btn {
    font-size: 13px;
  }
} */

.primary-btn:hover {
  color: aliceblue;
  background-color: #000000;
}

.highlighted-btn {
  color: #fff;

  font-size: 12px;
  background-color: #000000;
}

.highlighted-btn:hover {
  color: #fff;
  background-color: #8b0000;
}
@media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
}

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}

:root {
  --light-blue: #04c3ff;
  --light-blue-shadow: rgb(69 211 255);
  --sea-green: #0abead;
  --sea-green-dark: #0cbfae;
  --sea-green-extra-dark: #09bead;
  --sea-green-shadow: rgba(11, 190, 173, 0.4);
  --header-hamburger-background: rgba(11, 190, 173, 0.8);
  --orange: #f57f17;
  --dark-red: #851506;
  --orange-shadow: rgba(245, 127, 23, 0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message: #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow: #9c9c9ce3;
  --toast-success: #0cbfae;
  --loader-color: #0cbfae;
  --red: #da1d1dfa;
  --purple: #723ea2;
  --purple-transparent: #723ea27c;
  --dark-blue: #04127e;
  --light-red: #eb1b3d;
  --light-redTransparent: #eb1b3e6c;
  --green: #42CBA5;
  --dark-green: #316474;
}

.MainContent {
  flex: 1 1;
  background-attachment: fixed;
  position: relative;
}

:not(.skipContent):focus{
  outline: 1px solid #ffffff;
  outline: 1px solid var(--white);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.skipContent{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  text-align: center;
  background-color: transparent;
  color: #ffffff;
  color: var(--white);
  padding: .5rem;
  translate: 0 -100%;
  transition: translate 200ms ease-in-out;
  -webkit-transition: translate 200ms ease-in-out;
  -moz-transition: translate 200ms ease-in-out;
  -ms-transition: translate 200ms ease-in-out;
  -o-transition: translate 200ms ease-in-out;
}

.skipContent:focus{
  translate: 0;
  background-color: #723ea27c;
  background-color: var(--purple-transparent);
  z-index: 1000;
}

.skipContent:hover{
  cursor: pointer;
  text-decoration: none;
  color: #ffffff7c;
}

.home_Content {
  background-image: linear-gradient(
    #723ea27c,
    #eb1b3e6c);
  background-image: linear-gradient(
    var(--purple-transparent),
    var(--light-redTransparent)
  );
}

header {
  background-color: #1f1e1e7c;
}

.products {
  background-image: linear-gradient(
    #eb1b3e6c,
    #723ea27c);
  background-image: linear-gradient(
    var(--light-redTransparent),
    var(--purple-transparent)
  );
  height: auto;
}

.productsSecond {
  background-image: linear-gradient(
    #723ea27c,
    #eb1b3e6c);
  background-image: linear-gradient(
    var(--purple-transparent),
    var(--light-redTransparent)
  );
  height: auto;
}

::selection {
  color: rgba(26, 26, 25, 0.623);
  background: rgba(255, 255, 255);
}

